.container{
    width: auto;
    height: auto;
    background-color: #070B1C;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    position: relative;
}


.movie-poster{
    width: 100%;
    height: 100%;
    border-radius: 30px;
}

.box-image{
    width: 48%;
    height: 95%;
}

.box-description{
    width: 48%;
    height: auto;
    
}

.movie-title{
    font-size: 24px;
    margin: 0;
    color: #DB202C;
}

.move{
    width: 430px;
    height: 300px;
    background-color: #1B1F3F;
    border-radius: 30px;
    box-shadow: #451B4F 0px 2px 4px, #451B4F 0px 7px 13px -3px, #451B4F 0px -3px 0px inset;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .5rem;
    color: white;
    margin: 10px 0;
}

.saved{
    width: 100px;
    height: 30px;
    background-color: #DB202C;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    color: white;
    border: 0;
}

.saved:hover{
    background-color: #ab363e;
}

.fa-bookmark{
    color: #DB202C;
    position: absolute;
    top: 20px;
    right: 20px;
}

.savedIcon{
    color: gold;
}